
  

  .card-background:hover {
    box-shadow: 1px 2px 2px rgba(0, 0, 0, 0.2);
    transform: translateY(-5px);

  }


  
  .card-image {
    justify-content: center;
    align-self: center;
    position: relative;
    object-fit: contain;
    padding: 1rem; 
    padding-bottom: 0;
    height: 75%;
    width: same-as-height;
    margin: 0px;
    box-sizing: border-box;
    position: relative;

    top: 0;
    left: 0;
  }

  a {
    width: 100%;
    height: 100%;
  }
  
  .card-image img {
    width: 100%;
    height: 100%;
    margin: 0;
    z-index: 2;
  }
  
  .card-content {
    padding: 0rem;
    text-align: center;
    white-space: nowrap; 
    overflow: hidden;
    padding-top: 0;
    align-self: flex-end;
    height: 25%;
    box-sizing: border-box;
    padding-bottom: 0.2rem;
  }

  #silhouette {
    -webkit-filter: grayscale(100%) brightness(0);
    filter: grayscale(100%) brightness(0);
    opacity: 0.2;
    height: 14rem;
    width: 14rem;
    position: absolute;
    z-index: -1;
    left: 0.8rem;
    top: 0.2rem;
    transition: ease-in;

  }



  .card-content p {
    margin: 0;
    font-size: 1.3rem;
    white-space: nowrap;
    overflow: hidden;
  }
  
  .card-content h3 {
    margin: 0;
    font-size: 1.3rem;
    font-weight: bold;
    white-space: nowrap;
    overflow: hidden;
  }
  

  .card {
    height: 19rem;
    width: 15rem;
    padding: 0px;
  }

  .card-background
  {
    width: 100%;
    border-radius: 0.8rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    backface-visibility: hidden;
    transition: transform 0.3s ease-out;
    box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.2);
    overflow: hidden;
  }

